import React, { useState, useEffect, useCallback, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Dropdown, ConfigProvider, Spin, Empty } from "antd";
import { IoClose } from "react-icons/io5";
import { LuPhoneCall } from "react-icons/lu";
import "./style.css";
import { getAlertList } from "@/api/elderlySupport";
import { decodeHeartBreath, getAlertInfoViaEvent } from "@/utils/helper";
import dayjs from "dayjs";
import { getElderliesPriority } from "@/api/elderly";
import { useNavigate } from "react-router-dom";
import NurseRoomMap from "./nurseRoomMap";
import AlertCloseModal from "@/Components/ActiveAlerts/AlertCloseModal";
import { subscribeToMqtt, unsubscribeFromMqtt } from "@/api/deviceReports";

export default function NursingHomeRoomView({
  refreshAlertList,
  roomList = [],
  loadingsData,
}) {
  const [selectedRoom, setSelectedRoom] = useState(null);
  const navigate = useNavigate(); // Initialize the navigate function
  const [openAlertCloseModal, setOpenAlertCloseModal] = useState(false);
  const [selectedAlert, setSelectedAlert] = useState(null);

  const [style, setStyle] = useState({
    textColor: "text-white",
    bodyColor: "text-white",
    txtc: "#fff",
    bgc: "#fff",
  });
  const [data, setData] = useState({
    time: [],
    heartRate: [],
    breathRate: [],
  });

  const [liveRateData, setLiveRateData] = useState(null);
  const [lastUpdate, setLastUpdate] = useState(Date.now());
  const [alertList, setAlertList] = useState([]);
  const [loadingAlert, setLoadingAlert] = useState(false);
  const [page, setPage] = useState(1); // Pagination page state
  const [hasMore, setHasMore] = useState(true); // Tracks if more data is available
  const listRef = useRef(null);

  // const [roomList, setRoomList] = useState([]);
  // const [loadingsData, setLoadingData] = useState(true);
  // const getRoomListData = useCallback(() => {
  //   setLoadingData(true);
  //   getElderliesPriority()
  //     .then((res) => {
  //       setRoomList(res.data);
  //       setLoadingData(false);
  //     })
  //     .catch((err) => {
  //       console.log(err);
  //       setLoadingData(false);
  //     });
  // }, []);

  // useEffect(() => {
  //   getRoomListData();
  // }, [getRoomListData]);
  const getAlertListData = useCallback(() => {
    if (loadingAlert) return;
    setLoadingAlert(true);
    getAlertList({
      to_date: "2024-11-30",
      from_date: dayjs().format("YYYY-MM-DD"),
      is_resolved: false,
      elderly_id: selectedRoom?._id,
      limit: 20,
      page,
    })
      .then((res) => {
        setAlertList((prev) => [...prev, ...res.data]);
        setLoadingAlert(false);
      })
      .catch((err) => {
        setLoadingAlert(false);
        console.error(err);
      });
  }, [selectedRoom?._id, page]);

  useEffect(() => {
    if (selectedRoom?._id) {
      setAlertList([]); // Clear alert list
      setPage(1); // Reset to the first page
    }
  }, [selectedRoom?._id]);

  useEffect(() => {
    if (selectedRoom?._id) {
      getAlertListData();
    }
  }, [getAlertListData]);

  const handleScroll = () => {
    const { scrollTop, scrollHeight, clientHeight } = listRef.current;

    // Check if user has scrolled to the bottom
    if (scrollTop + clientHeight >= scrollHeight - 10 && !loadingAlert) {
      setPage((prevPage) => prevPage + 1);
    }
  };
  useEffect(() => {
    if (selectedRoom) {
      setStyle({
        textColor:
          getAlertInfoViaEvent(selectedRoom)?.label !==
          ("Critical" || "Warning" || "Info")
            ? "text-primary"
            : "text-white",
        bodyColor:
          getAlertInfoViaEvent(selectedRoom)?.label !==
          ("Critical" || "Warning" || "Info")
            ? "text-primary"
            : "text-white",
        txtc:
          getAlertInfoViaEvent(selectedRoom)?.label !==
          ("Critical" || "Warning" || "Info")
            ? "#fff"
            : "#000",
        bgc:
          getAlertInfoViaEvent(selectedRoom)?.label !==
          ("Critical" || "Warning" || "Info")
            ? "#fff"
            : "#000",
      });
    }
  }, [selectedRoom]);
  const getInitials = (name = "") => {
    const splitName = name?.split(" ");
    return `${splitName[0]?.[0]?.toUpperCase() || ""}${
      splitName[1]?.[0]?.toUpperCase() || ""
    }`;
  };

  useEffect(() => {
    const deviceCode =
      selectedRoom?.rooms?.length > 0
        ? selectedRoom.rooms
            .filter((item) => item.device_no)
            .map((item) => item.device_no)
            .join(", ")
        : "";

    if (deviceCode && selectedRoom) {
      subscribeToMqtt({
        uid: deviceCode,
        messageType: ["1", "2", "3", "4"],
        topics: {
          pub: [deviceCode],
        },
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }, [selectedRoom]);
  // Decode action handler
  const handleDecode = (base64Input) => {
    const data = decodeHeartBreath(base64Input);
    setLiveRateData(data);
    setLastUpdate(Date.now());
  };

  useEffect(() => {
    const interval = setInterval(() => {
      const now = Date.now();
      const isStale = now - lastUpdate > 5000; // Data is stale if older than 5 seconds

      if (isStale) {
        setLiveRateData(null);
        setData({ time: [], heartRate: [], breathRate: [] }); // Reset to empty arrays, not just an empty array
      } else if (liveRateData) {
        const newTime = new Date().toLocaleTimeString();

        // Update the data state with liveRateData values
        setData((prevData) => ({
          time: [...prevData.time, newTime].slice(-30),
          heartRate: [...prevData.heartRate, liveRateData.heartRateValue].slice(
            -30
          ),
          breathRate: [
            ...prevData.breathRate,
            liveRateData.breathingValue,
          ].slice(-30),
        }));
      }
    }, 1000);

    return () => clearInterval(interval);
  }, [liveRateData, lastUpdate]);

  useEffect(() => {
    if (!selectedRoom) return;
    const deviceCode =
      selectedRoom?.rooms?.length > 0
        ? selectedRoom.rooms
            .filter((item) => item.device_no)
            .map((item) => item.device_no)
            .join(", ")
        : "";

    const ws = new WebSocket("wss://live.elderlycareplatform.com/ws/");
    ws.onopen = () => {
      console.log("Connected to WebSocket server");
      // ws.send(JSON.stringify({ uid: deviceCode }));
    };
    ws.onmessage = (event) => {
      const data = JSON.parse(event?.data);
      const payload = JSON.parse(data?.message)?.payload;
      if (deviceCode.includes(payload?.deviceCode) && payload?.heartbreath) {
        handleDecode(payload.heartbreath);
      }
    };
    ws.onerror = (error) => {
      console.error("WebSocket error:", error);
    };
    ws.onclose = () => {
      console.log("Disconnected from WebSocket server");
    };
    return () => ws.close();
  }, [selectedRoom]);

  const handleViewClick = (row) => {
    window.scrollTo(0, 0);
    navigate(`/supporter/elderlies/elderly-profile/${row?._id}`); // Navigate to the profile page with the ID
  };

  const onCloseModal = () => {
    const deviceCode =
      selectedRoom?.rooms?.length > 0
        ? selectedRoom.rooms
            .filter((item) => item.device_no)
            .map((item) => item.device_no)
            .join(", ")
        : "";

    if (deviceCode && selectedRoom) {
      unsubscribeFromMqtt({
        uid: deviceCode,
        messageType: ["1", "2", "3", "4"],
      })
        .then((res) => {})
        .catch((err) => {
          console.log(err);
        });
    }
    setSelectedRoom(null);
  };

  if (loadingsData) {
    return (
      <>
        {loadingsData && (
          <span className="w-full h-full flex items-center justify-center">
            <Spin />
          </span>
        )}
      </>
    );
  }
  if (!roomList.length) {
    return (
      <div className="h-full flex items-center w-full justify-center">
        <Empty description="No room available to show" />
      </div>
    );
  }

  return (
    <>
      <div id="NurseRoomView" className="w-full h-full relative">
        <div className="w-full h-full grid grid-cols-10  overflow-auto">
          {roomList?.map((item, ind) => (
            <motion.div
              key={ind}
              layoutId={item?._id}
              onClick={() => setSelectedRoom(item)} // Store the entire room object
              className={`text-white text-base ${
                getAlertInfoViaEvent(item?.alarms_data[0])?.label === "Critical"
                  ? "bg-[#FF1D1D] animate-scaleUpDown1"
                  : getAlertInfoViaEvent(item?.alarms_data[0])?.label ===
                    "Warning"
                  ? "bg-[#FF9831] animate-scaleUpDown2"
                  : getAlertInfoViaEvent(item?.alarms_data[0])?.label === "Info"
                  ? "bg-[#6471FF]"
                  : "bg-[#F4F4F6] !text-primary"
              }  rounded-lg cursor-pointer flex justify-center m-2 items-center font-semibold min-w-[55px] min-h-[65px] h-fit `}
            >
              <motion.h5>{item?.room_no || "--"}</motion.h5>
            </motion.div>
          ))}
        </div>
        <AnimatePresence>
          {selectedRoom && (
            <motion.div
              className="absolute inset-0 flex justify-center items-center"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
            >
              <motion.div
                className="bg-black bg-opacity-50 w-full h-full flex justify-center items-center"
                onClick={() => onCloseModal()}
              >
                <motion.div
                  className={`${
                    getAlertInfoViaEvent(selectedRoom)?.label === "Critical"
                      ? "bg-[#FF1D1D]"
                      : getAlertInfoViaEvent(selectedRoom)?.label === "Warning"
                      ? "bg-[#FF9831]"
                      : getAlertInfoViaEvent(selectedRoom)?.label === "Info"
                      ? "bg-[#6471FF]"
                      : "bg-[#F4F4F6]"
                  } p-4 rounded-xl w-[95%] h-[90%]`}
                  // style={{ margin: "50px" }}
                  onClick={(e) => e.stopPropagation()}
                  layoutId={selectedRoom?._id}
                >
                  <motion.div className="flex justify-between items-start">
                    {" "}
                    {/* <motion.div
                      className={`rounded-xl flex flex-col items-start justify-start w-[35%]`}
                    >
                      <motion.h2
                        className={`leading-none font-semibold ${style.bodyColor} opacity-70`}
                      >
                        Room Number
                      </motion.h2>
                      <motion.h5
                        className={`text-4xl font-bold leading-none ${style.textColor}`}
                      >
                        {selectedRoom?.room_no || "--"}
                      </motion.h5>
                    </motion.div> */}
                    <div
                      className="w-[65%] flex items-start cursor-pointer bg-white hover:bg-white/75 rounded-xl transition-all duration-300 ease-in"
                      onClick={() => handleViewClick(selectedRoom)}
                    >
                      <div
                        id="elderly-profile"
                        className="flex items-center w-full  justify-between px-4 "
                      >
                        <div className="flex items-center gap-2">
                          {/* <div
                            id="image"
                            className="relative rounded-full min-h-[60px]  h-[60px] min-w-[60px] w-[60px] bg-[#fff] flex items-center justify-center font-semibold text-xl"
                          > */}
                          {/* <div className="flex flex-col items-center gap-2">
                            {selectedRoom.alertStatus !== "normal" && (
                              <span
                                className={`animate-ping absolute inline-flex h-full w-full rounded-full ${
                                  selectedRoom.alertStatus === "critical"
                                    ? "bg-red-50"
                                    : selectedRoom.alertStatus === "warning"
                                    ? "bg-orange-50"
                                    : selectedRoom.alertStatus === "normal"
                                    ? "bg-none"
                                    : "bg-blue-50"
                                } opacity-75`}
                              ></span>
                            )}
                            <span
                              className={`relative rounded-full h-[80px] w-[80px] ${
                                selectedRoom.alertStatus === "critical"
                                  ? "bg-red-500 border-red-500"
                                  : selectedRoom.alertStatus === "warning"
                                  ? "bg-orange-500 border-orange-500"
                                  : selectedRoom.alertStatus === "normal"
                                  ? "bg-green-500 border-green-500 !border-0"
                                  : "bg-blue-500 border-blue-500"
                              } flex items-center justify-center overflow-hidden border-0`}
                            >
                              <img
                                className="w-full h-full object-cover overflow-hidden"
                                src="https://www.shutterstock.com/image-photo/elderly-hispanic-man-grey-hair-260nw-2511514837.jpg"
                                alt=""
                              />
                            </span>
                          </div> */}
                          {/* <img
                            className="w-full h-full object-cover overflow-hidden"
                            src="https://www.shutterstock.com/image-photo/elderly-hispanic-man-grey-hair-260nw-2511514837.jpg"
                            alt=""
                          /> */}
                          {/* {getInitials(selectedRoom?.name)} */}
                          {/* </div> */}

                          <div className="flex flex-col items-start w-full gap-1">
                            <h2
                              className={`text-xl font-bold ${style.textColor} text-center text-nowrap m-0  leading-none`}
                            >
                              {selectedRoom?.name}
                            </h2>
                            <h2
                              className={`text-base font-medium ${style.textColor} opacity-80 text-center text-nowrap m-0 leading-none`}
                            >
                              Age: <span>{selectedRoom?.age}</span>
                            </h2>
                          </div>
                        </div>

                        <div
                          id="liveHeartAndBreathRate"
                          className="flex w-fit py-4 gap-8"
                        >
                          <div
                            id="heartRate"
                            className="flex flex-col items-start justify-start w-full"
                          >
                            <h2
                              className={`text-[27px] font-bold leading-none ${style.textColor}  text-nowrap`}
                            >
                              {liveRateData?.heartRateValue ?? "--"}{" "}
                              <span className="text-sm">/BPM</span>
                            </h2>
                            <h2
                              className={`text-sm font-medium ${style.textColor} opacity-80 text-nowrap`}
                            >
                              Heart Activity
                            </h2>
                          </div>
                          <div
                            id="breathRate"
                            className="flex flex-col items-start justify-start w-full"
                          >
                            <h2
                              className={`text-[27px] font-bold leading-none ${style.textColor}  text-nowrap`}
                            >
                              {liveRateData?.breathingValue ?? "--"}{" "}
                              <span className="text-sm">/BPM</span>
                            </h2>
                            <h2
                              className={`text-sm font-medium ${style.textColor} opacity-80  text-nowrap`}
                            >
                              Breathing Pattern
                            </h2>
                          </div>
                        </div>
                      </div>
                    </div>
                    <motion.button
                      className="p-2 bg-black/5  hover:bg-black/10 rounded-full"
                      onClick={() => onCloseModal()}
                    >
                      <IoClose size={24} className={style.textColor} />
                    </motion.button>
                  </motion.div>
                  <motion.div className="flex h-[80%] items-center mt-2">
                    <motion.div className="h-full  w-[65%] bg-white rounded-xl flex flex-col items-center justify-center">
                      <NurseRoomMap
                        rooms={selectedRoom?.rooms?.filter(
                          (room) => room.device_no
                        )}
                      />
                    </motion.div>
                    <motion.div
                      className="h-full w-[35%] rounded-xl p-2 border-1 overflow-y-auto overflow-x-hidden gap-2 flex flex-col pt-0"
                      ref={listRef}
                      onScroll={handleScroll}
                    >
                      {alertList?.map((alarm, indx) => (
                        <div
                          className="flex items-center gap-2 w-[350px] bg-white p-2 rounded-lg cursor-pointer"
                          key={indx}
                          onClick={() => {
                            setSelectedAlert(alarm);
                            setOpenAlertCloseModal(true);
                          }}
                        >
                          <div id="Icon">
                            {getAlertInfoViaEvent(alarm).icon ? (
                              <img
                                src={getAlertInfoViaEvent(alarm).icon}
                                alt="Alert Icon"
                                width={38}
                                height={38}
                                className="min-w-[38px]"
                              />
                            ) : null}
                          </div>
                          <div className="w-full">
                            <h4 className="text-sm font-medium text-text-primary">
                              {getAlertInfoViaEvent(alarm)?.title}
                            </h4>
                            <p className="text-xs text-text-secondary">
                              {getAlertInfoViaEvent(alarm)?.message}
                            </p>
                          </div>
                        </div>
                      ))}
                      {alertList.length === 0 && !loadingAlert && (
                        <div className="h-full flex items-center w-full justify-center">
                          <Empty description="No alerts available to show" />
                        </div>
                      )}
                      {loadingAlert && (
                        <div className="h-full w-full flex flex-col justify-center items-center">
                          <Spin />
                        </div>
                      )}
                    </motion.div>
                  </motion.div>
                </motion.div>
              </motion.div>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
      <AlertCloseModal
        openAlertCloseModal={openAlertCloseModal}
        setOpenAlertCloseModal={setOpenAlertCloseModal}
        selectedAlert={selectedAlert}
        getAlertListDatas={() => {
          getAlertListData();
          refreshAlertList();
        }}
      />
    </>
  );
}
