import React, { useState, useEffect, useCallback } from "react";

import { Segmented, ConfigProvider, Select } from "antd";
import { HomeOutlined, AimOutlined } from "@ant-design/icons";
import NursingHomeRoomView from "./Components/RoomvView/NursingHomeRoomView";
import MultiLineChart from "./Components/3xLineChart/MultiLineChart";
import WeeklyPicker from "@/Components/WeeklyPicker/WeeklyPicker";
import WeeklyMonthlyPicker from "@/Components/WeeklyMonthlyPicker/WeeklyMonthlyPicker";
import CustomButton from "@/Shared/button/CustomButton";
import { DatePicker, Input } from "antd";
const { RangePicker } = DatePicker;
import { SidebarContext } from "@/Context/CustomUsertable";
import CustomTable from "@/Shared/Table/CustomTable";
import { LuPlus } from "react-icons/lu";
import { useAlertTableColumns } from "../Utiles/utiles";
import { LuNavigation } from "react-icons/lu";
import {
  activeAlertsTableData,
  recentlyClosedAlertsTableData,
  eventsTableData,
} from "./mock";
import ActiveAlertsCards from "@/Components/ActiveAlerts/ActiveAlertsCards";
import CreateAndEditModal from "@/Components/CreateAndEditModal/CreateAndEditModal";
import { getAlertList, getEventList } from "@/api/elderlySupport";
import dayjs from "dayjs";
import AlertsOverview from "./Components/AlertsOverview/alertsOverview";
import { getAlertsGroup } from "@/utils/helper";
import { getElderlies, getElderliesPriority } from "@/api/elderly";

export default function NurseDashboard() {
  const [viewType, setViewType] = useState("Map View");
  const [modalOpen, setModalOpen] = useState(false);
  const [expandedRowKeys, setExpandedRowKeys] = useState([]);
  const activeAlertTableColumns = useAlertTableColumns("activeAlert");
  const recentlyClosedAlertTableColumns = useAlertTableColumns(
    "recentlyClosedAlert"
  );
  const eventsTableColumns = useAlertTableColumns("events");
  function handleDatePicker(dateString) {
    console.log(dateString);
  }
  const onExpand = (expanded, record) => {
    if (expanded) {
      setExpandedRowKeys([record._id]);
    } else {
      setExpandedRowKeys([]);
    }
  };

  const [roomList, setRoomList] = useState([]);
  const [loadingsData, setLoadingData] = useState(true);
  const [eventList, setEventList] = useState([]);
  const [eventPage, setEventPage] = useState([]);
  const [page, SetPage] = useState(1);
  const [page2, setPage2] = useState(1);
  const [alertList, setAlertList] = useState([]);
  const [recentlyClosedAlert, setRecentlyClosedAlert] = useState([]);
  const [alertQuery, setAcitveAlertQuery] = useState({});
  const [historyAlertQuery, setHistoryAlertQuery] = useState({});
  const [historyAlartLoading, setHistoryAlartLoading] = useState(true);
  const [activeAlartLoading, setActiveAlartLoading] = useState(true);
  const [searchResult, setSearchResult] = useState();
  const [activeAlertTotal, setActiveAlertTotal] = useState();
  const [searchQuery, setSearchQuery] = useState("");
  const [isFocused, setIsFocused] = useState(false);
  const getAlertListData = useCallback(() => {
    setActiveAlartLoading(true);
    getAlertList({
      to_date: "2024-11-30",
      from_date: dayjs().format("YYYY-MM-DD"),
      is_resolved: false,
      ...page,
      ...getAlertsGroup("Critical"),
      // ...alertQuery,
    })
      .then((res) => {
        setAlertList(res?.data);
        setActiveAlertTotal(res?.total);
        setActiveAlartLoading(false);
      })
      .catch((err) => {
        setActiveAlartLoading(false);
      });
  }, [page]);
  // }, [page, alertQuery]);

  useEffect(() => {
    getAlertListData();
  }, [getAlertListData]);

  const getRecentlyClosedAlerts = useCallback(() => {
    setHistoryAlartLoading(true);
    getAlertList({
      to_date: "2024-11-30",
      from_date: dayjs().format("YYYY-MM-DD"),
      is_resolved: true,
      page: page2,
      ...getAlertsGroup("Critical"),
      // ...historyAlertQuery,
    })
      .then((res) => {
        setRecentlyClosedAlert(res.data);
        setHistoryAlartLoading(false);
      })
      .catch((err) => {
        console.log(err);
        setHistoryAlartLoading(false);
      });
  }, [page2]);
  // }, [page2, historyAlertQuery]);

  useEffect(() => {
    getRecentlyClosedAlerts();
  }, [getRecentlyClosedAlerts]);
  const getAllEventList = useCallback(() => {
    getEventList({
      to_date: "2024-11-30",
      from_date: dayjs().format("YYYY-MM-DD"),
      page: eventPage,
    })
      .then((res) => {
        if (res.data.length > 0) {
          setEventList(res.data);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, [eventPage]);
  useEffect(() => {
    getAllEventList();
  }, [getAllEventList]);

  const getRoomListData = useCallback(() => {
    setLoadingData(true);
    getElderliesPriority()
      .then((res) => {
        setRoomList(res.data);
        setLoadingData(false);
      })
      .catch((err) => {
        console.log(err);
        setLoadingData(false);
      });
  }, []);

  useEffect(() => {
    getRoomListData();
  }, [getRoomListData]);
  function refreshAlertList() {
    getAlertListData();
    getRecentlyClosedAlerts();
    getRoomListData();
  }
  function onAlertActiveSegmantChnage(value) {
    setAcitveAlertQuery(getAlertsGroup(value));
  }
  function onAlertHistorySegmantChnage(value) {
    setHistoryAlertQuery(getAlertsGroup(value));
  }

  const getElderlyBySearch = useCallback(() => {
    if (searchQuery.trim() === "") {
      setSearchResult([]); // Clear results if the search query is empty
      return;
    }

    getElderlies({
      search: searchQuery,
    })
      .then((res) => {
        // const originalData = ;
        // const duplicatedData = [
        //   ...originalData,
        //   ...originalData.map((item) => ({
        //     ...item,
        //     _id: `${item._id}_duplicate`, // Create a unique ID for the duplicate
        //   })),
        // ];

        setSearchResult(res?.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [searchQuery]);
  useEffect(() => {
    getElderlyBySearch();
  }, [getElderlyBySearch]);
  const highlightText = (text, query) => {
    if (!query) return text;
    const regex = new RegExp(`(${query})`, "gi");
    return text.replace(
      regex,
      `<mark style="background-color: #80CAA7; color: white;">$1</mark>`
    );
  };
  return (
    <>
      <div className="flex gap-4 flex-col pt-8 pb-8">
        <div className="flex item-center justify-between w-full">
          <ConfigProvider
            theme={{
              token: {
                fontFamily: "Baloo2",
                colorPrimary: "#8086AC",
                colorLinkActive: "#8086AC",
                colorLinkHover: "#8086AC",
                colorLink: "#8086AC",
              },
            }}
          >
            <div className="w-[350px] relative">
              <Input
                className="rounded-[10px]  min-w-[350px] w-full "
                size="large"
                placeholder="Search Anything"
                style={{ height: "44px" }}
                onChange={(e) => setSearchQuery(e.target.value)}
                onFocus={() => setIsFocused(true)}
                // onBlur={() => setIsFocused(false)}
              />
              {isFocused && searchQuery.trim() !== "" && (
                <div
                  id="searchResult"
                  className="mt-3 max-h-[600px] overflow-auto rounded-lg shadow-lg bg-white border border-gray-300 absolute top-10 left-0 z-[1000] w-full"
                >
                  <ul>
                    {searchResult?.map((result) => (
                      <li
                        key={result._id}
                        className="p-4 hover:bg-gray-100 border-b border-gray-200 cursor-pointer"
                        onClick={() => {
                          navigate(
                            `/supporter/elderlies/elderly-profile/${result?._id}`
                          );
                        }}
                      >
                        <div className="font-semibold text-lg text-gray-800">
                          <span
                            dangerouslySetInnerHTML={{
                              __html: highlightText(result.name, searchQuery),
                            }}
                          />
                        </div>
                        <div className="text-sm text-gray-500">
                          <strong>Age:</strong>{" "}
                          <span
                            dangerouslySetInnerHTML={{
                              __html: highlightText(
                                result.age.toString(),
                                searchQuery
                              ),
                            }}
                          />
                        </div>
                        <div className="text-sm text-gray-500">
                          <strong>Address:</strong>{" "}
                          <span
                            dangerouslySetInnerHTML={{
                              __html: highlightText(
                                result.address || "N/A",
                                searchQuery
                              ),
                            }}
                          />
                        </div>

                        <div className="text-sm text-gray-500">
                          <strong>Sensitivity Factors:</strong>{" "}
                          <span
                            dangerouslySetInnerHTML={{
                              __html: highlightText(
                                result.diseases.join(", "),
                                searchQuery
                              ),
                            }}
                          />
                        </div>
                        <div className="text-sm text-gray-500">
                          <strong>Comments:</strong>{" "}
                          <span
                            dangerouslySetInnerHTML={{
                              __html: highlightText(
                                result.comments
                                  .map((c) => c.comment)
                                  .join(", "),
                                searchQuery
                              ),
                            }}
                          />
                        </div>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </ConfigProvider>
        </div>
        <div className="w-full">
          <ActiveAlertsCards
            refreshAlertList={refreshAlertList}
            isElderlyVisible={true}
          />
        </div>
        <div className="w-full flex flex-col gap-4 lg3:flex-row">
          <div className="w-full p-0">
            <div className="bg-white rounded-2xl p-6 h-[650px] flex flex-col">
              <div className="flex justify-between items-center mb-5">
                <h1 className="text-2xl font-bold">Room View</h1>
              </div>
              <div className="w-full h-full overflow-y-auto rounded-xl">
                <NursingHomeRoomView
                  refreshAlertList={refreshAlertList}
                  roomList={roomList}
                  loadingsData={loadingsData}
                />
              </div>
            </div>
          </div>
          <div className="bg-white w-full lg3:w-[48%] rounded-2xl p-6 h-[650px] overflow-hidden">
            <AlertsOverview />
          </div>
        </div>

        <div
          id="Active_Alerts"
          className="w-full flex flex-col gap-4 justify-center items-center bg-white rounded-2xl p-6"
        >
          <div
            id="Active_Alerts_Header"
            className="w-full flex justify-between"
          >
            <h1 className="text-[24px] font-bold">Active Notifications</h1>
            <div>
              {/* <ConfigProvider
                theme={{
                  components: {
                    Segmented: {
                      itemSelectedBg: "#252F67",
                      itemSelectedColor: "#fff",
                      fontFamily: "Baloo2",
                    },
                  },
                }}
              >
                <Segmented
                  options={activeAlertSegmentOptions}
                  onChange={(value) => onAlertActiveSegmantChnage(value)}
                />
              </ConfigProvider> */}
            </div>
          </div>
          <div id="Active_Alerts_Table" className="w-full">
            <SidebarContext.Provider
              value={{
                total: activeAlertTotal,
                page: 1,
                SetPage,
              }}
            >
              <CustomTable
                pageSize={6}
                loading={activeAlartLoading}
                tableData={alertList}
                columns={activeAlertTableColumns}
              />
            </SidebarContext.Provider>
          </div>
        </div>
        <div
          id="Recently_Closed_Alerts"
          className="w-full flex flex-col gap-4 justify-center items-center bg-white rounded-2xl p-6"
        >
          <div
            id="Recently_Closed_Alerts_Header"
            className="w-full flex justify-between"
          >
            <h1 className="text-[24px] font-bold">Past Notifications</h1>
            <div>
              {/* <ConfigProvider
                theme={{
                  components: {
                    Segmented: {
                      itemSelectedBg: "#252F67",
                      itemSelectedColor: "#fff",
                      fontFamily: "Baloo2",
                    },
                  },
                }}
              >
                <Segmented
                  options={activeAlertSegmentOptions}
                  onChange={(value) => onAlertHistorySegmantChnage(value)}
                />
              </ConfigProvider> */}
            </div>
          </div>
          <div id="Recently_Closed_Alerts_Table" className="w-full">
            <SidebarContext.Provider
              value={{
                total: recentlyClosedAlert?.length,
                page: 1,
                SetPage: setPage2,
              }}
            >
              <CustomTable
                pageSize={6}
                loading={historyAlartLoading}
                tableData={recentlyClosedAlert}
                columns={recentlyClosedAlertTableColumns}
              />
            </SidebarContext.Provider>
          </div>
        </div>
        <div
          id="Events"
          className="w-full flex flex-col gap-4 justify-center items-center bg-white rounded-2xl p-6"
        >
          <div id="Events_Header" className="w-full flex justify-between">
            <h1 className="text-[24px] font-bold">Events</h1>
          </div>
          <div id="Events_Table" className="w-full">
            <SidebarContext.Provider
              value={{
                total: eventList?.length,
                page: 1,
                SetPage: setEventPage,
                getList: () => getAllEventList,
              }}
            >
              <CustomTable
                pageSize={6}
                tableData={eventList}
                columns={eventsTableColumns}
                // expandable={{
                //   expandedRowRender: (record) => (
                //     <div
                //       style={{ margin: "0" }}
                //       className="flex gap-14 w-full items-center justify-start px-2"
                //     >
                //       <div className="flex w-[50vw] items-center justify-between">
                //         <div>
                //           <h2 className="text-[16px] font-semibold">
                //             {record.hospital.name}
                //           </h2>
                //           <p className="text-base">
                //             Phone Number:{" "}
                //             {record.hospital.phone
                //               ? record.hospital.phone
                //               : "Not Available"}
                //           </p>
                //         </div>
                //         <div className="flex items-center gap-2 ">
                //           <a
                //             href={`https://www.google.com/maps?q=${record.hospital.latitude},${record.hospital.longitude}`}
                //             target="_blank"
                //             rel="noopener noreferrer"
                //             className="text-base font-medium !text-blue-500"
                //           >
                //             View on Google Maps
                //           </a>
                //           <LuNavigation size={18} className="text-blue-500" />
                //         </div>
                //       </div>
                //     </div>
                //   ),
                //   rowExpandable: (record) => record.elderlyName !== null,
                //   expandedRowKeys: expandedRowKeys, // Controlled expanded row keys
                //   onExpand: onExpand, // Handle expand/collapse events
                // }}
              />
            </SidebarContext.Provider>
          </div>
        </div>
      </div>
      <CreateAndEditModal
        modalOpen={modalOpen}
        setModalOpen={setModalOpen}
        type="event"
      />
    </>
  );
}

export const alertViewSegmentOptions = [
  {
    label: ` Map View`,
    value: "Map View",
    icon: <AimOutlined />,
  },
  {
    label: "Room View",
    value: "Room View",
    icon: <HomeOutlined />,
  },
];
export const activeAlertSegmentOptions = [
  {
    label: "All Alerts",
    value: "All Alerts",
  },
  {
    label: "Critical",
    value: "Critical",
  },
  {
    label: "Warning",
    value: "Warning",
  },
  {
    label: "Informational",
    value: "Info",
  },
];
