import React, { useCallback, useState, useEffect, useRef } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Room1 from "@/Components/RoomMap/RoomMap3";
import Room2 from "@/Components/RoomMap/RoomMap4";
import "./style.css";
import { Empty } from "antd";
import useWebSocket from "@/hook/useWebSoket";
import { decodePosition } from "@/utils/helper";

export default function NurseRoomMap({ rooms = [] }) {
  const [roomData, setRoomData] = useState({}); // Store room-specific data
  const timeoutRefs = useRef({}); // Track timeouts for each device
  const heartBreathRateRefs = useRef({}); // Track heart breath activity for each device
  const lastHeartBreathTimestamps = useRef({}); // Track last heartbreath timestamps for each device

  const settings = {
    dots: rooms.length >= 2,
    infinite: rooms.length > 2,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    draggable: true,
    arrows: false,
    appendDots: (dots) => (
      <div
        style={{
          backgroundColor: "transparent",
          borderRadius: "10px",
          padding: "2px",
        }}
      >
        <ul id="dots" style={{ margin: "0px" }}>
          {" "}
          {dots}{" "}
        </ul>
      </div>
    ),
  };

  const handleDecode = useCallback((code, deviceNo) => {
    const data = decodePosition(code);

    // Modify people data if heartBreathRateActive is true
    if (heartBreathRateRefs.current[deviceNo] && data[0]?.postureIndex === 4) {
      data[0].postureIndex = 6;
      data[0].posture = "Resident on Bed";
      data[0].color = "#252F67";
    }

    setRoomData((prev) => ({
      ...prev,
      [deviceNo]: {
        ...prev[deviceNo],
        peopleData: data,
      },
    }));
  }, []);

  const handleHeartBreathUpdate = useCallback((deviceNo) => {
    // Set heartBreathRateActive to true for the device
    heartBreathRateRefs.current[deviceNo] = true;
    lastHeartBreathTimestamps.current[deviceNo] = Date.now();

    // Clear any existing timeout for this device
    if (timeoutRefs.current[deviceNo]) {
      clearTimeout(timeoutRefs.current[deviceNo]);
    }

    // Set a timeout to reset heart breath activity after 5 seconds
    timeoutRefs.current[deviceNo] = setTimeout(() => {
      heartBreathRateRefs.current[deviceNo] = false;

      // Reset the posture and update the data for this device
      setRoomData((prev) => ({
        ...prev,
        [deviceNo]: {
          ...prev[deviceNo],
          peopleData: prev[deviceNo]?.peopleData?.map((person) => ({
            ...person,
            postureIndex: person.postureIndex === 6 ? 4 : person.postureIndex,
            posture: person.postureIndex === 6 ? "Standing" : person.posture,
            color: person.postureIndex === 6 ? "#A7E2FE" : person.color,
          })),
        },
      }));
    }, 5000);

    // Immediately set heartBreathRateActive to true in roomData
    setRoomData((prev) => ({
      ...prev,
      [deviceNo]: {
        ...prev[deviceNo],
        heartBreathRateActive: true,
      },
    }));
  }, []);

  useWebSocket({
    onMessage: (data) => {
      const payload = JSON.parse(data?.message)?.payload;

      if (payload?.position) {
        handleDecode(payload.position, payload.deviceCode);
      }

      if (payload?.heartbreath) {
        handleHeartBreathUpdate(payload.deviceCode);
      }
    },
    dependencies: [handleDecode, handleHeartBreathUpdate],
  });

  useEffect(() => {
    const interval = setInterval(() => {
      const now = Date.now();

      // Check each device's last heart-breath timestamp
      Object.keys(lastHeartBreathTimestamps.current).forEach((deviceNo) => {
        if (
          lastHeartBreathTimestamps.current[deviceNo] &&
          now - lastHeartBreathTimestamps.current[deviceNo] > 5000
        ) {
          heartBreathRateRefs.current[deviceNo] = false;

          // Reset the posture and update the data for this device
          setRoomData((prev) => ({
            ...prev,
            [deviceNo]: {
              ...prev[deviceNo],
              heartBreathRateActive: false,
              peopleData: prev[deviceNo]?.peopleData?.map((person) => ({
                ...person,
                postureIndex:
                  person.postureIndex === 6 ? 4 : person.postureIndex,
                posture:
                  person.postureIndex === 6 ? "Standing" : person.posture,
                color: person.postureIndex === 6 ? "#A7E2FE" : person.color,
              })),
            },
          }));
        }
      });
    }, 1000);

    return () => {
      clearInterval(interval);
      Object.values(timeoutRefs.current).forEach((timeout) => {
        clearTimeout(timeout);
      });
    };
  }, []);

  return (
    <>
      {rooms.length > 0 && (
        <div
          id="rooms"
          className="w-full mb-2 transition-all duration-300 h-full"
        >
          <Slider
            {...settings}
            className="w-full transition-all duration-300 h-full"
          >
            {rooms.map((room, index) => (
              <div
                key={index}
                className="px-2 overflow-hidden transition-all duration-300 w-full h-[355px] "
              >
                <div
                  id="roomNmaeAndStatus"
                  className="flex items-center gap-2 w-fit bg-white pr-4 py-1.5 border border-gray-200 p-2 rounded-full absolute top-2 left-2"
                >
                  <div
                    id="dot"
                    className="size-3 rounded-full"
                    style={{
                      backgroundColor:
                        room?.is_device_bind === true ? "green" : "red",
                    }}
                  ></div>
                  <p>{room?.name}</p>
                </div>
                {room?.is_device_bind && room?.mount_type === 2 && (
                  <Room2
                    roomInfo={room}
                    position={roomData[room.device_no]?.peopleData}
                  />
                )}
                {room?.is_device_bind && room?.mount_type === 1 && (
                  <Room1
                    roomInfo={room}
                    position={roomData[room?.device_no]?.peopleData}
                  />
                )}
                {!room?.is_device_bind && (
                  <span className="w-full h-full flex items-center justify-center">
                    <p>No Device Data Available</p>
                  </span>
                )}
              </div>
            ))}
          </Slider>
        </div>
      )}
      {rooms.length === 0 && <Empty description="No Room Found " />}
    </>
  );
}
